export default {
	name: "",
	components: {

	},
	props: {},
	data() {
		return {
			user: {},
			choose:null
		}
	},
	methods: {
		back() {
			uni.navigateTo({
				url: "/pages/edited/edited"
			})
		},
		choosesex(e){
			this.choose=e
		},
		editsex(){
			if(this.choose===true){
				this.user.sex=0
			}else{
				this.user.sex=1
			}
			console.log(this.user.sex)
			uni.request({
				url: `${this.$api}/user/modifyUserinfo?qq=122212489`,
				method: 'POST',
				header: {
					"headerUserId": this.user.id,
					"headerUserToken": this.user.userUniqueToken
				},
				data: {
					birthday:this.user.birthday,
					nickname:this.user.nickname,
					sex:this.user.sex,
					userId:this.user.id
				},
				success: res => {
					console.log(res);
					uni.setStorageSync('user',this.user)
					uni.navigateTo({
						url:"/pages/edited/edited"
					})
				},
				fail: () => {},
				complete: () => {}
			});
		}
	},
	mounted() {

	},
	onLoad() {
		this.user = uni.getStorageSync('user')
	},
	filters: {

	},
	computed: {

	},
	watch: {

	},
	directives: {

	}
}
